import React from 'react'
import { Container,Row,Col } from 'react-bootstrap'
import { useRef } from 'react';
import emailjs from '@emailjs/browser';

export default function Contact(props) {
    
  const form = useRef();
//   const ref=useRef(null);
//   const nameref=useRef(null);
//   const emailref=useRef(null);
  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_mcvcfcn', 'template_6i538tl', form.current, 'KskX1JSBm8Ap2LM0V')
      .then((result) => {
          console.log(result.text);
          console.log("Message sent!!!");
      }, (error) => {
          console.log(error.text);
      });
    //   nameref.current.value="";
    //   emailjsref.current.value="";
    //   ref.current.value="";
    e.target.reset();
  };

  return (
    <div className="contact" id='contact'>

    <Container>
        <Row className='mb-5 mt-3'>
            <Col lg='8'>
                <h1 className=' mb-4'>Contact Me</h1>
            </Col>
        </Row>
        <Row className='sec_sp'>
            <Col lg='5' className='mb-5'>
                <h2 className='color_sec py-4 touch'>Get in touch</h2>
                <address>
                    <div className="add">

                    <strong>Email:</strong> rajatmahajan514@gmail.com
                    <br />
                    <br />
                    <p>
                        <strong>Phone:</strong> 9822964723
                    </p>
                    </div>
                    <a href="https://www.instagram.com/__rajatt_/" target='_blank' rel="noreferrer"><img className='instalogo' src="insta.png" alt="instalogo" /></a>
                    <a href="https://www.linkedin.com/in/rajat-mahajan-074b13229/" target='_blank' rel="noreferrer"><img className='linkedin' src="linkedin.png" alt="linkedin" /></a>
                    <a href="https://github.com/Rajatdev01" target='_blank' rel="noreferrer"><img className='github' src="github.png" alt="github" /></a>
                </address>
                <p>Feel free to connect!</p>
            </Col>
            <Col lg='7' className='d-flex align-items-center'>
                <form className='contact__form w-100' ref={form} onSubmit={sendEmail}>
                    <Row>
                        <Col lg='6' className='form-group'>
                            <input type="text" className='form-control' id='name' name='name'
                            placeholder='Name'
                            />
                        </Col>
                        <Col lg='6' className='form-group rounded-0'>
                            <input type="email" className='form-control' id='email' name='email' 
                            placeholder='Email'
                            />
                        </Col>
                    </Row>
                    <textarea name="message" id="message" rows="5" 
                    className='form-control rounded-0 my-3' placeholder='Message'>
                    </textarea>
                    <br />
                    <Row>
                        <Col lg='12' className='form-group'>
                            <button className='btn ac_btn' type='submit'>Send</button>
                        </Col>
                    </Row>
                </form>
            </Col>
        </Row>
    </Container>
        </div>
  )
}