import React from "react";
import Skills from "./Skills";

export default function About(props) {

  return (
    
    <div id="about">
      <>
        <div className="about py-5">
          <div className="container py-5">
            <div className="row">
              <div className="col-lg-5">
                <img className="img-fluid aboutimg" src="aboutimg.png" alt="coding" />
              </div>
              <div className="col-lg-7">
                <h1>About Me</h1>
                <p className="aboutp">WEB DESIGNER AND GRAPHIC DESIGNER</p>
                <p className="py-3 info">Hello,<br />I am Rajat Mahajan,<br />Currently practicing as a Graphic Designer and a Web Developer. <br />Pursuing B.Tech. degree in VIIT,Pune.</p>
              </div>
            </div>
          </div>
        </div>
        <Skills mode={props.mode}/>
      </>
    </div>
  );
}
